@import url(https://fonts.googleapis.com/css?family=Anton&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* overflow-y: hidden; */
}
.title {
  width: 100%;
  padding: 70px 0px 70px 0px;
  text-align: center;
  font-size: 50px;
  font-weight: 700;
  letter-spacing: 1.6px;
  color: #333333;
  font-family: 'Anton', sans-serif;
  background-color: #f8efe4; }

.pixabayImg {
  width: 100%;
  height: 300px;
  text-align: center;
  padding: 20px 0px 20px 0px;
  opacity: 1;
  display: block;
  transition: 0.7s ease;
  -webkit-transition: 0.7s ease;
  -moz-transition: 0.7s ease;
  -ms-transition: 0.7s ease;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden; }

.middleImg {
  transition: 0.7s ease;
  -webkit-transition: 0.7s ease;
  -moz-transition: 0.7s ease;
  -ms-transition: 0.7s ease;
  transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  -moz-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  text-align: center; }
  .middleImg .pixabayBtn {
    color: white;
    font-size: 25px; }

.pixabayImg-container:hover .pixabayImg {
  opacity: 0.5;
  transform: scale(1.1);
  -webkit-transform: scale(1.1);
  -moz-transform: scale(1.1);
  -ms-transform: scale(1.1); }

.pixabayImg-container:hover .middleImg {
  opacity: 1; }

.pixabayContainer {
  width: 100%;
  margin: 70px 0 0 0; }
  .pixabayContainer .form {
    width: 100%;
    position: relative;
    height: 100px;
    overflow: hidden;
    text-align: left; }
  .pixabayContainer .form input {
    width: 50%;
    height: 100%;
    color: #595f6e;
    padding-top: 40px;
    border: none;
    outline: none; }
  .pixabayContainer .form label {
    position: absolute;
    bottom: 0px;
    left: 0%;
    width: 50%;
    height: 100%;
    pointer-events: none;
    border-bottom: 1px solid black; }
  .pixabayContainer .form label::after {
    content: "";
    position: absolute;
    height: 100%;
    left: 0px;
    bottom: -1px;
    width: 100%;
    border-bottom: 3px solid #5fa8d3;
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
    -moz-transform: translateX(-100%);
    -ms-transform: translateX(-100%);
    -webkit-transition: -webkit-transform 0.1s ease;
    transition: -webkit-transform 0.1s ease;
    transition: transform 0.1s ease;
    transition: transform 0.1s ease, -webkit-transform 0.1s ease;
    -webkit-transition: transform 0.1s ease;
    -moz-transition: transform 0.1s ease;
    -ms-transition: transform 0.1s ease; }
  .pixabayContainer .content-search {
    position: absolute;
    bottom: 5px;
    left: 0px;
    transition: all 0.1s ease;
    -webkit-transition: all 0.1s ease;
    -moz-transition: all 0.1s ease;
    -ms-transition: all 0.1s ease; }
  .pixabayContainer .form input:focus + .label-search .content-search,
  .pixabayContainer .form input:valid + .label-search .content-search {
    transform: translateY(-140%);
    -webkit-transform: translateY(-140%);
    -moz-transform: translateY(-140%);
    -ms-transform: translateY(-140%);
    font-size: 14px;
    color: #5fa8d3; }
  .pixabayContainer .form input:focus .label-search::after,
  .pixabayContainer .form input:valid + .label-search::after {
    transform: translateX(0%);
    -webkit-transform: translateX(0%);
    -moz-transform: translateX(0%);
    -ms-transform: translateX(0%); }
  .pixabayContainer .search-btn {
    margin-left: 20px; }
  .pixabayContainer .btn-section {
    height: 100px;
    padding-top: 50px;
    text-align: right; }
    .pixabayContainer .btn-section .toggle-btn {
      margin-left: 30px; }
  .pixabayContainer .result-section {
    margin-top: 70px; }

@media screen and (max-width: 425px) {
  .form input {
    width: 100%; }
  .form label {
    width: 100%; }
  .search-btn {
    margin-left: 0px; } }

.app {
  font-family: 'Roboto', sans-serif; }

